const Search = props => (
    <div>
        <input type="text" className="searchBar" placeholder="Search"></input>
          
  
      <style jsx>{`
        .searchBar{
          background-color: white;
          height: 20px;
          
          padding-left: 5px;
          // margin-right: 2px;
          width: calc(100% - 22px);
          border-radius: 100px;
        }
      `}
      </style>
    </div>
  );
  
  export default Search;